
import React from 'react';


function mapIcon(socialMedia) {
    const media = socialMedia.replace(/ /g, '-');
    switch (socialMedia) {
        default:
            return (
                <i className={media}/>
            );
    }
}

const SocialMedia = ({profiles=[]}) => {

    return (
        <ul className=''>
            {profiles.map(function (profile, index) {
                const icon = mapIcon(profile.network.toLowerCase());
                return (
                    <li key={index}>
                        <a href={profile.url}>
                            {icon}
                        </a>
                    </li>
                );
            })}
        </ul>
    );
};

export default SocialMedia
