

import React from 'react';

import {Datetime} from '../../utils/datetime';

const Entry = ({entry={}}) => {
    
    const startDate = Datetime.getDisplayFromDate(entry.startDate);
    const endDate = Datetime.getDisplayFromDate(entry.endDate);
    return (
        <div className="mx-3 p-6 bg-white border rounded-lg overflow-hidden">
            <h3 className="mt-6 text-lg font-bold text-gray-900 leading-tight" >{entry.institution}</h3>
            <p className=''>
                {entry.area}
                <span> &bull; </span>
                <span className=''>{entry.summary}</span>
                <span> &bull; </span>
                <em className=''>{startDate} - {endDate}</em>
            </p>
        </div>
    );
};



const Education  = ({education=[]}) => {


    

    return (
        <section id='education'>
            <div className="px-8 py-12">
                <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">Education</h1>
            </div>
            <div className="px-8">
                {education.map(function (entry, index) {
                    return (
                <Entry key={index} entry={entry}/>
                    );
                })}
            </div>
        </section>
    );

}


export default Education 
