

import React from 'react';

// import Link from './link';
import {navigation} from '../../prop_types/resume';

import logo from '../../logo.svg'

import { ReactComponent as BarsSvg} from '../../svg/bars.svg'
import { ReactComponent as ExisSvg} from '../../svg/close.svg'



function Link({link, name, className}) {
    return (
        <li className={className} >
            <a href={`#${link}`}>{name}</a>
        </li>
    );
}

 

export default class Navigation  extends React.Component {

    isOpen = false

    handleMenu() {
        this.isOpen= !this.isOpen
        return this.setState({
            isOpen: !this.isOpen
        });
    }

    render() {
        return (
            <nav id='nav-wrap' className="fixed right-0 left-0 bg-orange-900 text-white ">
                <div className="sm:hidden" >
                    <div className=" p-2 flex justify-between" >
                        <img className="object-contain h-6" src={logo} alt="mylogo" ></img>
                        <button onClick={()=> this.handleMenu()}  type="button" className="text-gray-500 hover:text-white focus:text-white" >
                            { !this.isOpen ? <BarsSvg className="h-6 w-6 fill-current" ></BarsSvg> : <ExisSvg className="h-6 w-6 fill-current" ></ExisSvg> }
                        </button>
                    </div>
                    <ul id='nav' className={this.isOpen? "block px-4 text-font-semibold":"hidden"} >
                        {Object.keys(this.props.navigation).map(function (navigationLink, index) {
                            const navigationName = this.props.navigation[navigationLink];
                            return (
                        <Link key={index} link={navigationLink} name={navigationName} className={"px-3 py-1 text-sm uppercase hover:bg-gray-700 rounded-md w-full"} />
                            );
                        }.bind(this))}
                    </ul>
                </div>
                <ul id="nav" className="hidden flex items-center px-4 sm:flex justify-between sm:justify-center">
                    {Object.keys(this.props.navigation).map(function (navigationLink, index) {
                        const navigationName = this.props.navigation[navigationLink];
                        return (
                            <Link key={index} link={navigationLink} name={navigationName} className={"px-3 py-1 text-sm uppercase"} />
                        );
                    }.bind(this))}
                </ul>
            </nav>
        );
    }
};

Navigation.propTypes = {
    navigation: navigation
}

