

import React from 'react';
import PropTypes from 'prop-types';

import ReactModal from 'react-modal';

import {projects} from '../../prop_types/resume';

// ReactModal.setAppElement('#yourAppElement')

export default class Modal  extends React.Component {

    render() {
        const style = {
            overlay: {
                backgroundColor: 'rgba(0, 0, 0, 0.75)',
            }
        };

        return (
            <ReactModal isOpen={this.props.isOpen} onRequestClose={this.props.onRequestClose} style={style} ariaHideApp={false} className="max-h-full bg-white m-10 border border-white rounded-lg" >
                <div className="px-8 py-12">
                    <h5 className="mt-6 text-lg font-bold text-gray-900 leading-tight" >{this.props.entry.name}</h5>
                    <p className="mt-6 text-gray-700 leading-tight" >{this.props.entry.summary}</p>
                    <span className="m-6 flex text-center">
                        {this.props.entry.keywords.map(function (keyword, index) {
                            return <p key={index} className="m-1 p-1 w-1/6 rounded-md bg-gray-600 text-white text-sm" >{keyword}</p>
                        })}
                    </span>
                </div>
                <div className="flex px-8 py-12 justify-around">
                    <a className="px-3 py-1 text-sm uppercase bg-green-500 hover:bg-gray-700 rounded-md"
                        href={this.props.entry.website || '#/'}
                        target='_blank'
                        rel='noopener noreferrer'>
                        Open
                    </a>
                    <a className="px-3 py-1 text-sm text-white uppercase bg-red-500 hover:bg-gray-700 rounded-md" 
                    href='#/' onClick={this.props.onRequestClose}>Close</a>
                </div>
            </ReactModal>
        );
    }
};


Modal.propTypes = {
    entry: projects,
    isOpen: PropTypes.bool.isRequired,
    onRequestClose: PropTypes.func.isRequired
}
