

import React from 'react';



export class Skill  extends React.Component {
    state = {
        style: {
            background: '#313131'
        }
    }

    render() {
        const style = this && this.state.style

        return (
            <div className="px-8 py-12 m-3 p-6 bg-white border rounded-lg overflow-hidden">
                <h3 className="mt-6 text-lg font-bold text-gray-900 leading-tight">{this.props.title}</h3>
                <p className=''>{this.props.description}</p>
                <div className="px-8 py-12">
                    <ul className="flex">
                        {this.props.keywords.map(function (entry, index) {
                            return (
                                <li className="m-1 p-1 w-1/6 rounded-md bg-gray-600 text-white text-sm " key={index}>
                                    <span
                                        className={`bar-expand percentage90`}
                                        style={style}/>
                                    <em >{entry}</em>
                                </li>

                            );
                        })}
                    </ul>
                </div>
            </div>
        );
    }
};


const Skills = ({skills = [] }) => {
        return (
            <section id="skill" className="px-8 py-12">
                <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">
                    <span>Skills</span>
                </h1>
                <div className="">
                    {skills.map(function (skill, index) {
                        return (
                    <Skill key={index} title={skill.title} description={skill.description} keywords={skill.keywords}/>
                        );
                    })}
                    {/*
                        <Skill title='Languages' content={content.languages}/>
                    */}
                </div>
            </section>
        );
};


export default Skills