

import React from 'react';

// import {Random} from '../../utils/random';
import Modal from './modal';

const Entry = ({entry={}}) => {

    const [modalOpen, setModalOpen] = React.useState(false);  
 
    const handleOpenModal = () => {
        setModalOpen(true)
    }

    const handleCloseModal = () => {
        setModalOpen(false)
    }

    return (
        <div className="h-20 w-1/4 m-2 p-3 bg-gray-400 border rounded-lg overflow-hidden justify-center hover:bg-green-400"  >
            <div className="px-2 py-3" onClick={handleOpenModal} >
                <h5 className="text-lg font-bold text-gray-900 leading-tight">{entry.name}</h5>
                <p>{entry.category}</p>
            </div>
            <Modal entry={entry} isOpen={modalOpen} onRequestClose={handleCloseModal}/>
        </div>
    );
};


const Portfolio  = ({portfolio=[]}) => {    
    
    return (
        <section id='portfolio' className=" px-8 py-12">
            <h1 className="mt-12 text-lg font-bold text-gray-900 leading-tight" >Portfolio</h1>
            <div id='portfolio-wrapper' className='flex-wrap flex justify-between m-3 p-6 bg-white border rounded-lg overflow-hidden'>
                {portfolio.map(function (entry, index) {
                    return (
                        <Entry key={index} index={index} entry={entry} />
                    );
                })}
            </div>
        </section>
    );
};

export default Portfolio
