

import React from 'react';

import Carousel from 'nuka-carousel';

const Entry = ({entry={}}) => {
    return (
        <div className="mt-0 p-20 sm:px-40 bg-white border rounded-lg overflow-hidden" >
            <blockquote>
                <p className="text-green-700 text-lg    " >{entry.reference}</p>
                <cite className="text-bold">
                    {entry.name}
                    <span className="block">
                    {`${entry.position}, ${entry.company}`}
                    </span>
                </cite>
            </blockquote>
        </div>
    );
};


const References  =({references=[]}) => {
    console.log(references);
    


    const carouselConfig = {
        autoplay: true,
        decorators: [],
        framePadding: '10px',
        cellSpacing: 30,
        wrapAround: true
    };


    return (
        <section id='references' >
            <div className="px-8 py-12">
                <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">References</h1>
            </div>
            <div className="px-12">
                <div className=''>
                    <Carousel
                        autoplay={carouselConfig.autoplay}
                        decorators={carouselConfig.decorators}
                        wrapAround={carouselConfig.wrapAround}>
                        {references.map(function (entry, index) {
                            return (
                                <Entry key={index} entry={entry} />
                            );
                        })}
                    </Carousel>
                </div>
            </div>
        </section>
    );
};

export default References

