import React from 'react';

import request from 'superagent';
import jsyaml from 'js-yaml'

import './tailwind.generated.css';

import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'; // Link
// Router custom history

// import Main from './components/main';
import Home from './components/home';
import NotFound from './components/errors/not_found';

function getResumePath() {  
  if (['localhost', '127.0.0.1'].indexOf(window.location.hostname) !== -1) {
    console.log('dev', window.location.hostname) // DEV mode ?
  }
  return '/resume.yaml';
}

function getNavigation() {
  return {
      home: 'Home',
      about: 'About',
      education: 'Education',
      skill: 'Skills',
      portfolio: 'Portfolio',
      references: 'References',
      work: 'Work',
  };
}

function getResume(path, callback){
  return request
  .get(path)
  .end(callback);
}

function convertYamlToJson(yaml){
  let json = jsyaml.load(yaml)
  return json
}

const App = () =>  {
  console.log('App')

  const resumePath=getResumePath()
  const navigation=getNavigation()

  const [resumeJson, setResumeJson] = React.useState('');  

  if(!resumeJson) {
    getResume(resumePath, function (error, response) {
      if(!error && !resumeJson) {
        const yaml = response.text
        const json = convertYamlToJson(yaml)
        setResumeJson(json)
      }
    })
    return <p>loading...</p>
  }

 
  return (
    <div className="text-align-center antialiased text-gray-900">
      <Router>
            <div>
                <Switch>
                    <Route render={(props) => <Home resume={resumeJson} navigation={navigation} />} />
                    <Route path='*' component={NotFound}/>
                </Switch>
            </div>
      </Router>
    </div>
  );
}

export default App;
